onreadystatechange = () => {\n            if (document.readyState == \"complete\") {\n                this.setSplider();\n            }\n        };\n        window.addEventListener(\"resize\", this.setWidth);\n        this.$nextTick(() => {\n            this.$refs[this.link].scrollIntoView({\n                behavior: \"smooth\",\n                block: \"start\",\n                inline: \"nearest\",\n            });\n        });\n    },\n};\n</script>\n\n<style scoped>\n.guest-invi-navbar-cont {\n    position: fixed;\n    display: flex;\n    justify-content: center;\n    top: 91px;\n    z-index: 9999999999;\n    width: 100%;\n}\n.guest-invi-navbar {\n    background: #461952;\n    border-radius: 27px;\n    padding: 8px 13px !important;\n}\n/* .guest-invi-navbar::-webkit-scrollbar {\n    display: none;\n} */\n.guest-invi-navbar a {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 15px;\n    line-height: 22px;\n    text-align: center;\n    color: #9a9a9a;\n    padding: 8px 40px;\n    cursor: pointer;\n    white-space: nowrap;\n    text-decoration: none;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n.guest-invi-navbar a > span {\n    width: 46px;\n    height: 46px;\n    margin-bottom: 25px;\n    border-radius: 50%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    background: rgba(255, 255, 255, 0.1);\n}\n.guest-invi-navbar .active {\n    background: #ffffff;\n    border-radius: 23px;\n    color: #461952;\n    font-weight: 700;\n}\n.guest-invi-navbar > li {\n    border: none !important;\n}\n/* Navbar css end */\n@media screen and (max-width: 768px) {\n    .guest-invi-navbar-cont {\n        top: 61px;\n    }\n    .guest-invi-navbar {\n        width: 100%;\n        max-width: 100% !important;\n        border-radius: 0 !important;\n    }\n    .guest-invi-navbar .active {\n        background: inherit;\n        border-radius: 0;\n     